import React, { useState } from "react";
import { useAuth0 } from "@auth0/auth0-react";
import { Form, Input, message } from "antd";
import axios from "axios";
import { useNavigate, Link } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import DateFnsUtils from "@date-io/date-fns";
import Typography from "@material-ui/core/Typography";
// import Footer from "../../components/FooterComp/FooterComp";
import {
  DatePicker,
  TimePicker,
  MuiPickersUtilsProvider,
} from "@material-ui/pickers";
import { User } from "@auth0/auth0-react";
// import { Alert } from 'bootstrap';

const useStyles = makeStyles((theme) => ({
  formContainer: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    height: "80vh",
  },
  form: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    padding: theme.spacing(3),
    backgroundColor: "#fff",
    borderRadius: theme.spacing(1),
    boxShadow: "0px 5px 10px rgba(0, 0, 0, 0.25)",
    [theme.breakpoints.down("sm")]: {
      width: "100%",
    },
    [theme.breakpoints.up("md")]: {
      width: "70%",
    },
  },
  formHeading: {
    marginBottom: theme.spacing(3),
  },
  formInput: {
    margin: theme.spacing(1),
    width: "100%",
  },
  formButton: {
    marginTop: theme.spacing(3),
  },
}));

const AppointmentForm = () => {
  const navigate =useNavigate();
  const { user, loginWithRedirect, isAuthenticated, logout } = useAuth0();
  const classes = useStyles();
  const [selectedDate, handleDateChange] = useState(new Date());
  const [selectedTime, handleTimeChange] = useState(new Date());
  const [name, setName] = useState("Name");
  const [email, setEmail] = useState("Email");
  const [phoneNumber, setPhoneNumber] = useState("+91");
  const [reason, setReason] = useState("Illness");
  const [doctor, setDoctor] = useState("Dr");
  const [age, setAge] = useState("21");
  const onFinishHandler = async (values) => {
    console.log("Printing values")
    console.log(values)
    console.log({name:name,email:email,phoneno: phoneNumber,reason:reason,docname:doctor,age:age,date:selectedDate,time:selectedTime})
    console.log("End of printing values")
    try {
      // dispatch(showLoading())
      const res = await axios.post(
        "/api/v1/appointmentuser/appointment",
        {name:name,email:email,phoneno: phoneNumber,reason:reason,docname:doctor,age:age,date:selectedDate,time:selectedTime},
      );
     
      if (res.data.success) {
        message.success("Appointment Request Sent Successful");
        setEmail("");
        setPhoneNumber("");
        setReason("");
        setDoctor("");
        setAge("");
        handleDateChange(new Date());
        handleTimeChange(new Date());
        navigate("/");
      }
    } catch (error) {
      console.log(error);
      message.error("Error in submitting!!");
      setEmail("");
      setPhoneNumber("");
      setReason("");
      setDoctor("");
      setAge("");
      handleDateChange(new Date());
      handleTimeChange(new Date());
    }
  };

  return (
    <div className={classes.formContainer}>
      <form  className={classes.form}>
        <Typography variant="h4" className={classes.formHeading}>
          Schedule an Appointment
        </Typography>
        <Grid container spacing={2}>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              fullWidth
              variant="outlined"
              label="Name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              className={classes.textField}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              fullWidth
              variant="outlined"
              label="Email"
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className={classes.textField}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              fullWidth
              variant="outlined"
              label="Phone Number"
              name="phoneno"
              type="tel"
              value={phoneNumber}
              onChange={(e) => setPhoneNumber(e.target.value)}
              className={classes.textField}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              fullWidth
              variant="outlined"
              name="reason"
              label="Reason for Appointment"
              value={reason}
              onChange={(e) => setReason(e.target.value)}
              className={classes.textField}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              fullWidth
              variant="outlined"
              name=""
              label="Doctors Name"
              value={doctor}
              onChange={(e) => setDoctor(e.target.value)}
              className={classes.textField}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              required
              fullWidth
              variant="outlined"
              label="Age"
              value={age}
              onChange={(e) => setAge(e.target.value)}
              className={classes.textField}
            />
          </Grid>

          <MuiPickersUtilsProvider utils={DateFnsUtils}>
            <Grid item xs={12} sm={6}>
              <DatePicker
                label="Select Date"
                value={selectedDate}
                onChange={handleDateChange}
                animateYearScrolling
                className={classes.formInput}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TimePicker
                label="Select Time"
                value={selectedTime}
                onChange={handleTimeChange}
                className={classes.formInput}
              />
            </Grid>
          </MuiPickersUtilsProvider>
        </Grid>
        <Button
          type="submit"
          variant="contained"
          color="primary"
          className={classes.formButton}
          onClick={onFinishHandler}
        >
          Schedule
        </Button>
      </form>
    </div>
  );
};

export default AppointmentForm;
